

.App {
  margin: 2em;
}

.App-link {
  color: #61dafb;
}

.loading {
  font-size: 0.95em;
  color: var(--main-darker-color);
}

.resultContainer {
  margin-top: 14px;
  font-size: 1.1em;
}

a.flickrUrl {
  color: var(--main-darker-color);
}

.details {
  margin-top: 0px;
  color: var(--main-darker-color);
  font-size: 0.8em;
}

.loading:after {
  overflow: hidden;
  display: inline-block;
  vertical-align: bottom;
  -webkit-animation: ellipsis steps(4,end) 900ms infinite;
  animation: ellipsis steps(4,end) 900ms infinite;
  content: "\2026"; /* ascii code for the ellipsis character */
  width: 0px;
}

@keyframes ellipsis {
  to {
    width: 1.25em;
  }
}

@-webkit-keyframes ellipsis {
  to {
    width: 1.25em;
  }
}

form.search {
  font-weight: 500;
}